import { Component } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable }     from 'rxjs/Observable';
import { Word } from "./word.model";
import { ApiService } from "./api.service";
import './rxjs-operators';
declare var jQuery: any;

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: [
		'./app.component.css'
	],
    providers: [
		ApiService
	]
})
export class AppComponent {
    word: Word;
	errorMessage: any;

    constructor(private wordService: ApiService) {
		this.runPermanentAnimations();
		this.getWord();
		this.setupAnimationElements();
    }

	button() {
		this.runAnimation();
		setTimeout(() => this.getWord(), 200);
	}

	runPermanentAnimations() {
		setInterval(() => {
			this.eyesAnimation();
		}, 5000);
	}

	eyesAnimation() {
	}

	runAnimation() {

	}

	leftEye3: string;
	rightEye3: string;
	setupAnimationElements() {
		this.leftEye3 = '#eye-l-3';
		this.rightEye3 = '#eye-r-3';
	}

	getWord() {
        this.wordService.getRandomWord().subscribe(
			word => {
				this.word = <Word><any>word;
			},
			e => console.error(e),
			() => console.log('finished')
		);
	}
}



